import {connect} from 'react-redux'
import {logout} from "../../redux/actions/login_action"
import React from 'react'
import axios from "axios"
import { Divider, Card, Image, Button, Space, Collapse} from 'antd'
import { useNavigate } from 'react-router-dom'

import './index.scss'

const { Panel } = Collapse

function VerificationUI (props) {
    const navigate = useNavigate()

    const gridStyle = {
        width: '33%',
        textAlign: 'center',
    }

    const [imgList, setImgList] = React.useState([
        {'image': 'error'},
        {'image': 'error'},
    ])

    const [descriptionId, setDescriptionId] = React.useState(0)
    const [sentence, setSentence] = React.useState("null")
    const [action, setAction] = React.useState('null')
    const [targetId, setTargetId] = React.useState(null)

    const [objectList, setObjectList] = React.useState([{'id':-1, 'image': 'error'}])
    const [choiceIndex, setChoiceIndex] = React.useState(0)

    React.useEffect(()=>{
        axios.post(
            '/backend/verification/get_data',
            {
                user_id: props.state.user_id,
                password: props.state.password,
            }
        ).then(
            function(response) {
                if (response.data.result === true) {
                    setDescriptionId(response.data.description_id)
                    setSentence(response.data.sentence)
                    setAction(response.data.action)
                    setImgList(response.data.image)
                    setObjectList(response.data.object)
                    setTargetId(response.data.target_id)
                } else {
                    alert(response.data.information)
                }
                
            },
            function(error) {
                console.log('失败', error)
            }
        )
    },[]) //仅在挂载和卸载的时候执行

    function submitToSystem (value) {
        // console.log(value)
        axios.post(
            '/backend/verification/post_data',
            {
                user_id: props.state.user_id,
                password: props.state.password,
                description_id: descriptionId,
                pass: value,
            }
        ).then(
            function(response) {
                // console.log('成功', response)
                alert(response.data.information)
                window.location.reload()
            },
            function(error) {
                alert("提交失败")
            }
        )
    }

    const toConfirm = () => {
        if (choiceIndex === 0) {
            alert("请选择一个目标")
        } else {
            submitToSystem(objectList[choiceIndex].id === targetId)
        }
    }


    const toBack = () => {
        navigate('/home', {
            replace: false,
            state: {},
        })
    }

    const clickLabel = (index=0, e) => {
        // console.log(objectList[choiceIndex].id === targetId)
        setChoiceIndex(index)
    }

    return (
        <div className='verificaton'>
            <div>
                <Space>
                    <h1>Grounding标注验证</h1>
                    <Button type='primary' danger onClick={toBack}>返回</Button>
                </Space>
                <Divider />
            </div>

            <div>
                <Collapse defaultActiveKey={['1']} >
                    <Panel header="描述" key="1">
                        <p>{sentence}</p>
                    </Panel>
                </Collapse>
            </div>

            <div>
                <Collapse defaultActiveKey={['2']} >
                    <Panel header="动作" key="2">
                        <p>{action}</p>
                    </Panel>
                </Collapse>
            </div>
            
            <div>
                <Card title="连续三帧图片">
                    <Card.Grid hoverable={false} style={gridStyle}>
                        <Image
                            width={240}
                            height={240}
                            src={imgList[0].image}
                            fallback="data:image/png;base64,"
                        />
                    </Card.Grid>
                    <Card.Grid hoverable={false} style={gridStyle}>
                        <Image
                            width={240}
                            height={240}
                            src={imgList[1].image}
                            fallback="data:image/png;base64,"
                        />
                    </Card.Grid>
                    <Card.Grid hoverable={false} style={gridStyle}>
                        <Image
                            width={240}
                            height={240}
                            src={objectList[choiceIndex].image}
                            fallback="data:image/png;base64,"
                        />
                    </Card.Grid>
                </Card>
            </div>

            <div>
                <Space size={[8, 16]} wrap>
                    {objectList.map((object, index) => (
                        index !== 0 ? <Button key={index} onClick={clickLabel.bind(this, index)} danger={index === choiceIndex}>{index}</Button> : null
                    ))}
                </Space>
            </div>
            
            <div>
                <Button type='primary' onClick={toConfirm}>确认</Button>
            </div>
            
            <Divider />
        </div>
    )
}

export default connect(
    (state) => ({state: state.user}), 
    {
      logout: logout,
    }
  )(VerificationUI)